import React, { useState, useEffect } from 'react';
import { TextField, Button, Box, Divider, Avatar, Select, MenuItem } from '@mui/material';
import { sendApiRequest } from '../lib/api/public';
import notify from '../lib/notify';
import AuthLayout from '../components/AuthLayout';
import { renderField } from '../components/utils';
import Uploader from '../components/common/Uploader';
import { readFile } from '../lib/api/public';
import { timeZones } from '../components/constants';

const Profile = () => {
  const [loading, setLoading] = useState(false);
  const [firstName, setFirstName] = useState('');
  const [lastName, setLastName] = useState('');
  const [avatarUrl, setAvatarUrl] = useState('');

  const [email, setEmail] = useState('');
  const [phone, setPhone] = useState('');
  const [timezone, setTimezone] = useState('');

  const [password, setPassword] = useState('');
  const [confirmPassword, setConfirmPassword] = useState('');

  useEffect(() => {
    sendApiRequest({ path: '/users/me', method: 'GET' })
      .then((res) => {
        setFirstName(res.firstName);
        setLastName(res.lastName);
        setAvatarUrl(res.avatarUrl);
        setEmail(res.email);
        setPhone(res.phone);
        setTimezone(res.timezone);
      });
  }, []);

  const save = () => {
    if (!email) {
      notify('Email is required');
      return;
    }

    setLoading(true);

    sendApiRequest({
      path: '/users/me',
      method: 'POST',
      data: {
        firstName,
        lastName,
        avatarUrl,
        email,
        phone,
        timezone,
        password,
        confirmPassword
      }
    })
      .then(() => {
        notify('Profile updated successfully');
      }).catch((err) => {
        notify(err.message);
      }).finally(() => {
        setLoading(false);
      });
  }

  return (
    <AuthLayout title="Profile" isLoading={loading}>
      <Box sx={{ width: '50%' }}>
         {renderField({ label: 'Avatar', renderControl: () => {
           return (
             <Box>
              <Avatar src={readFile(avatarUrl)} />

              <br />

              <Uploader
                label='Upload'
                showList={false}
                multiple={false}
                onChange={(result) => {
                  setAvatarUrl(result.url);
                }}
                />
             </Box>
           )
          }})}

         {renderField({ label: 'First Name', renderControl: () => {
            return (
              <TextField fullWidth value={firstName} onChange={(e) => setFirstName(e.target.value)} />
            )
          }})}

         {renderField({ label: 'Last Name', renderControl: () => {
            return (
              <TextField fullWidth value={lastName} onChange={(e) => setLastName(e.target.value)} />
            )
          }})}

          {renderField({ label: 'Email', renderControl: () => {
            return (
              <TextField fullWidth value={email} onChange={(e) => setEmail(e.target.value)} />
            )
          }})}

          {renderField({ label: 'Phone', renderControl: () => {
            return (
              <TextField fullWidth value={phone} onChange={(e) => setPhone(e.target.value)} />
            )
          }})}

          {renderField({ label: 'Timezone', renderControl: () => {
            return (
              <Select value={timezone} onChange={(e) => setTimezone(e.target.value)}>
                {timeZones.map((timezone) => (
                  <MenuItem value={timezone}>{timezone}</MenuItem>
                ))}
              </Select>
            )
          }})}

          <Divider />

          <h3>Change Password</h3>

          <br />

          {renderField({ label: 'Password', renderControl: () => {
            return (
              <TextField type="password" fullWidth value={password} onChange={(e) => setPassword(e.target.value)} />
            )
          }})}

          {renderField({ label: 'Confirm Password', renderControl: () => {
            return (
              <TextField type="password" fullWidth value={confirmPassword} onChange={(e) => setConfirmPassword(e.target.value)} />
            )
          }})}

          <Button variant="contained" color="primary" onClick={save} sx={{ float: 'right' }}>Save</Button>
      </Box>
    </AuthLayout>
  );
};

export default Profile;
